
import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Observable, throwError, Subject } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import { Member } from 'src/app/shared/model/member.model';

@Injectable()
export class AuthService {

  // Le membre actuellement connecté (authentifié avec succès)
  connectedMember = new Subject<Member>();

  constructor(
    private http: Http
  ) { }

  logIn(login: string, password: string): Observable<Member> {
    return this.http.get('http://localhost:8081/api/accounts/log-in', {
      params: {
        login: login,
        password: password
      }
    })
      .pipe(
        map((response) => response.json()),
        catchError(
          (error) => {
            return throwError(error);
          }
        )
    );
  }

}
